import hexToRgba from 'hex-rgba';
import { primary } from './commonStyles';

export default () => (
  <section>
    <style jsx>
      {`
        section {
          background-image: url(../static/img/background_2.jpg), ${hexToRgba(primary, 0.5)}
          background-position: center
          background-size: cover
          height: 30rem
          width: 100%
        }
      `}
    </style>
  </section>
);



